body,
html {
  background-image: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)),
    url("https://freedesignfile.com/upload/2021/02/Blue-purple-gradient-background-vector.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  overflow: clip;
  min-width: 330px;
  height: 100%;
  margin: 0;
}

.wrapper {
  display: grid;
  grid-auto-flow: column;
  justify-content: center;
  justify-items: center;
  align-content: center;
}

.flipped {
  transform: rotateY(180deg);
}

.empty {
  cursor: default;
}

div {
  cursor: pointer;
  text-align: center;
  padding: 0 !important;
}

.flipped > div > p {
  transform: rotateY(180deg);
}

p {
  font-size: 14px;
  font-weight: bold;
  margin: 0 !important;
}
